@import url(https://fonts.googleapis.com/css2?family=Philosopher:ital,wght@0,700;1,400;1,700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Anton&display=swap);

  

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;

  }

.TodoDate{width:150px;height:25px;margin-bottom:10px;color:#868e96;font-family:'Philosopher', sans-serif}

.TodoTitle{text-align:center;font-family:'Anton', sans-serif;font-size:40px;color:#ffd43b;margin-bottom:19px}

